.contact {
  padding-top: 9px;
  padding-left: 31px;

  &--mobile {
    width: 200px;
    margin-right: 76px;
  }

  &--phone {
    width: 155px;
  }
}

.contact__link {
  position: relative;

  font-size: $font-size-link;
  line-height: 1.4;
  font-weight: 500;
  text-decoration: none;
  color: $text-clr-basic;

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}

.contact__icon {
  position: absolute;
  top: 1px;
  left: -30px;
}

.contact__text {
  margin: 0;
  margin-top: 2px;
}
