.user-nav {
  @include list-reset();
  margin-left: auto;
  margin-top: 10px;
}

.user-nav__link {
  display: flex;
  justify-content: flex-end;

  color: inherit;
  text-decoration: none;

  &:hover {
    opacity: 0.6;
  }
  &:active {
    opacity: 0.3;
  }
}

.user-nav__icon {
  width: 21px;
  height: 23px;
  padding-bottom: 3px;
  margin-right: 10px;
}
